import React from 'react';

export default ({person}) => (
    <>
        <p>  <b>{person.firstName + ' ' + person.lastName}</b></p>
        <p>
        : <br />
        <textarea readOnly value={person.description} style={{width: '40%'}}/>
        </p>

        <p> : <b>{person.address.streetAddress}</b></p>
        <p>: <b>{person.address.city}</b></p>
        <p>/: <b>{person.address.state}</b></p>
        <p>: <b>{person.address.zip}</b></p>

    </>
)